.section2-main {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.section2-phone {
  width: 50%;
  height: 100%;
}

.section2-text {
  width: 50%;
  height: 100%;
}

@media (max-width: 768px) {
  .section2-main {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .section2-phone {
    width: 100%;
    height: 75%;
    order: 1;
  }

  .section2-text {
    width: 100%;
    height: 25%;
    order: 2;
  }
}
